* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  background-image: linear-gradient(120deg, rgba(175,147,181,1) 28%,
  rgba(156,189,178,1) 45%,
  rgba(201,199,186,1) 58%);
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
